.App {
  text-align: center;
  color:#808080;
}
p{
  margin:0px;
}

.App-logo {
  height: 40vmin;
}


.App-header {
  background-color: #f8f8ff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.MuiToolbar-root{
  box-shadow:0 3px 3px -3px rgba(0,0,0,.2);
}
.App{
  background-color: #f8f8ff;
  color:#808080;
  height:100%;
}
.header{
  background-color:#FFF;
}
.page{
  min-height: 700px;
}
.profileCard,.workCard{
  width:80%;
  max-width:400px;
  padding:50px 15px;
  border-radius:10px;
  box-shadow: 0 2px 2px -2px rgba(0,0,0,0.3);
  margin:auto;
  background-color:#FFF;
}
.introduction{
  width:90%;
  max-width:400px;
  margin:auto;
}
.socialIcons{
  margin:10px auto;
}
.workCard{
  margin:30px auto;
}
.skill,.work,.internship,.other{
  width:80%;
  max-width:400px;
  padding:50px 15px;
  margin:40px  auto;
  background-color:#FFF;
  border-radius:10px;
  box-shadow: 0 2px 2px -2px rgba(0,0,0,0.3);
}

.App-footer{
  padding-top:200px;
  padding-bottom:50px;
}


.App-link {
  color: #09d3ac;
}
